import React from "react";
import { useEffect } from "react";
import { deleteTodo, getAllTodos } from "../redux/actions/index";
import Tabs from "./Tabs";
import Todoitem from "./Todoitem";
import { All_TODOS, ACTIVE_TODOS, DONE_TODOS } from '../redux/actions/type';
import { useDispatch, useSelector } from "react-redux";
const Todo = () => {
  const dispatch = useDispatch();
  const todos = useSelector((state) => state.todos);
  const currentTab = useSelector(state => state.currentTab);
  useEffect(() => {
    dispatch(getAllTodos());
  }, []);
  console.log(todos);

  const getTodo = () => {
    if (currentTab === All_TODOS) {
      return todos;
    } else if (currentTab === ACTIVE_TODOS) {
      return todos.filter(todo => !todo.done)
    } else if (currentTab === DONE_TODOS) {
     return todos.filter(todo => todo.done)
    }
  }
  const removeDoneTodo = () => {
    todos.forEach(({ done, _id }) => {
      if (done) {
        dispatch(deleteTodo(_id));
      }
    })
  }
  return (
    <article>
      <div class="mt-3">
        <Tabs currentTab={currentTab} />

        {
          todos.some(todo => todo.done) ? (<button onClick={removeDoneTodo} className="clear">
            Remove Done 
          </button>):''
        }
      </div>
      <ul className="mt-4">
        {getTodo().map((todo, index) => (
          <Todoitem key={todo._id} todo={todo} />
        ))}
      </ul>
    </article>
  );
};

export default Todo;
